.about__list-container {
  margin-top: 40px; }

.slider-container {
  margin: 50px 0;
  display: -webkit-box;
  flex-wrap: nowrap;
  overflow-x: scroll;
  justify-content: center; }

@media (min-width: 768px) {
  .slider-container {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 30px;
    overflow-x: unset; } }

@media (min-width: 992px) {
  .about__list-container {
    margin: 88px 0; }
    .about__list-container:last-child {
      margin-bottom: 58px; } }

@media (min-width: 1200px) {
  .slider-container {
    flex-wrap: nowrap;
    margin: 80px 0; } }
