.restourants__flex-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap; }

.restourants__section {
  width: 50%; }

.ymap-container {
  height: 455px;
  cursor: pointer; }

.restourants__list {
  padding-left: 0;
  list-style: none;
  width: 50%; }

.restourants__section-title {
  text-align: left;
  margin: 20px 0;
  font-weight: 500;
  font-family: TT Norms;
  line-height: normal;
  font-size: 36px;
  color: #3D3B3B; }

.restourant-item__content {
  font-size: 13px;
  font-weight: 400;
  font-family: TT Norms;
  line-height: 1.2;
  color: #808080; }

.restourant-item__title {
  font-size: 17px;
  font-weight: 700;
  font-family: TT Norms;
  line-height: 1.2;
  margin: 0;
  color: #3d3b3b; }

.restourant-item__time, .restourant-item__phone, .restourant-item__metro {
  font-family: TT Norms;
  font-size: 15px;
  line-height: 18px;
  color: #808080;
  text-decoration: none; }

@media (max-width: 1440px) {
  .restourants__map {
    width: 60%; }
  .restourants__list {
    width: 40%; } }

@media (max-width: 991px) {
  .restourants__map {
    width: 55%; }
  .restourants__list {
    width: 45%; } }

@media (max-width: 767px) {
  .restourants__section {
    width: 100%; }
  .ymap-container {
    height: 50vw; } }

@media (max-width: 576px) {
  .restourants__flex-container {
    margin: 0 20px; } }

@media (max-width: 340px) {
  .restourans-item__photo {
    width: 100%; } }
