.spend-points__title-container {
  display: flex;
  flex-direction: row-reverse;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  white-space: nowrap;
  margin-bottom: 5px; }

.spend-points__title {
  font-size: 15px;
  line-height: 18px;
  font-weight: 600;
  color: #3D3B3B;
  font-family: "TT Norms"; }

.spend-points-bonuses-mobile__amount {
  margin: 0 5px; }

.spend-points-bonuses {
  display: flex;
  align-items: center; }
  .spend-points-bonuses__desctop {
    display: none; }
  .spend-points-bonuses__hint {
    font-size: 13px;
    line-height: 15px;
    color: #808080;
    max-width: 208px; }

@media (min-width: 769px) {
  .spend-points .fake-checkbox {
    margin-top: 10px; }
  .spend-points__title-container {
    flex-direction: row;
    white-space: nowrap;
    margin-bottom: 20px;
    justify-content: flex-start;
    align-items: flex-start; }
  .spend-points__title {
    font-size: 36px;
    line-height: 42px;
    font-weight: 500;
    display: inline-block;
    white-space: normal;
    margin-left: 10px; }
  .spend-points-bonuses-mobile {
    display: none; }
  .spend-points-bonuses__desctop {
    display: inline-block;
    white-space: nowrap; }
  .spend-points-bonuses__amount {
    font-size: 23px;
    line-height: 27px;
    margin-right: 5px;
    font-size: 23px;
    line-height: 27px;
    font-weight: 500; }
  .spend-points-bonuses__hint {
    max-width: 181px;
    margin-left: 20px; } }

@media (min-width: 992px) {
  .spend-points__title {
    margin-left: 20px; }
  .spend-points-bonuses__desctop {
    margin-left: 47px; } }

@media (min-width: 1440px) {
  .spend-points .fake-checkbox {
    margin-top: 0; }
  .spend-points-bonuses__hint {
    max-width: 200px; }
  .spend-points__title-container {
    align-items: center;
    margin-bottom: 25px; } }
