.fake-checkbox {
  width: 20px;
  height: 20px; }
  .fake-checkbox__input {
    opacity: 0;
    position: absolute; }
  .fake-checkbox__input:checked + label:before {
    border: 1px solid #8DD161;
    background: #8DD161; }
  .fake-checkbox__input:checked + label:after {
    content: '';
    position: absolute;
    left: -13px;
    top: 9px;
    background: white;
    width: 2px;
    height: 2px;
    box-shadow: 2px 0 0 white, 4px 0 0 white, 4px -2px 0 white, 4px -4px 0 white, 4px -6px 0 white, 4px -8px 0 white;
    transform: rotate(45deg); }
  .fake-checkbox .fake-checkbox__label {
    display: inline;
    margin-left: 18px;
    position: relative; }
    .fake-checkbox .fake-checkbox__label:before {
      content: '';
      position: absolute;
      left: -18px;
      width: 18px;
      height: 18px;
      border-radius: 5px;
      background: white;
      border: 1px solid #ccc; }

@media (min-width: 768px) {
  .fake-checkbox {
    width: 26px;
    height: 26px; }
    .fake-checkbox__input:checked + label:after {
      left: -16px;
      top: 12px; }
    .fake-checkbox .fake-checkbox__label {
      margin-left: 24px; }
      .fake-checkbox .fake-checkbox__label:before {
        left: -24px;
        width: 24px;
        height: 24px; } }
