.history-orders__result {
  border-top: 1px solid #CCCCCC;
  padding-top: 20px; }

.history-orders__total {
  font-size: 17px;
  font-weight: 700; }

.history-orders__discount {
  display: flex;
  justify-content: space-between; }
  .history-orders__discount-title, .history-orders__discount-amount {
    font-family: "TT Norms";
    font-size: 13px;
    line-height: 15px;
    color: #808080;
    margin-bottom: 8px; }

@media (min-width: 769px) {
  .history-orders__result {
    border-top: 0; } }
