.promo {
  font-family: "TT Norms"; }
  .promo .spend-points__title {
    display: none; }
  .promo__mobile-title {
    font-size: 15px;
    line-height: 18px;
    color: #3D3B3B;
    margin: 0 0 6px; }
  .promo__container {
    display: flex;
    flex-wrap: wrap; }
  .promo input[type='text'] {
    width: 100%;
    margin-bottom: 0;
    padding: 5px 30px 5px 10px;
    border-radius: 6px;
    font-size: 13px;
    line-height: 15px;
    color: #3D3B3B;
    font-weight: 500; }
    .promo input[type='text']::placeholder {
      font-weight: 400; }
    .promo input[type='text'].success {
      background: #fff url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0iMTgiIGhlaWdodD0iMTMiIHZpZXdCb3g9IjAgMCAxOCAxMyIgZmlsbD0ibm9uZSIgeG1sbnM9Imh0dHA6Ly93d3cudzMub3JnLzIwMDAvc3ZnIj4NCjxwYXRoIGQ9Ik0yIDdMNiAxMUwxNiAyIiBzdHJva2U9IiM4REQxNjEiIHN0cm9rZS13aWR0aD0iMyIgc3Ryb2tlLWxpbmVqb2luPSJyb3VuZCIvPg0KPC9zdmc+DQo=) no-repeat;
      background-position: right 10px center; }
    .promo input[type='text'].error {
      border: 1px solid #FD7065; }
    .promo input[type='text']:focus {
      outline: 0; }
    .promo input[type='text'].own-margin {
      margin-bottom: 25px; }
  .promo button {
    width: 100%;
    max-height: 40px; }
  .promo__errors {
    width: 100%; }
    .promo__errors .validation-result__error {
      font-size: 10px;
      line-height: 12px; }
  .promo__discount {
    margin: 2px 0 10px;
    font-size: 10px;
    line-height: 18px;
    color: #808080; }

@media (min-width: 576px) {
  .promo__container.own-margin {
    margin-bottom: 25px; }
  .promo input[type='text'] {
    width: 56%;
    margin-right: 20px;
    order: 1; }
    .promo input[type='text'].own-margin {
      margin-bottom: 0; }
  .promo button {
    width: 39%;
    order: 2; }
  .promo__errors {
    order: 3; }
    .promo__errors .validation-result__error {
      font-size: 13px; }
  .promo__discount {
    font-size: 13px;
    order: 3; } }

@media (min-width: 769px) {
  .promo__container.own-margin {
    margin-bottom: 0; }
  .promo .spend-points__title {
    display: block;
    margin: 0 0 28px; }
  .promo__mobile-title {
    display: none; }
  .promo input[type='text'] {
    width: 100%;
    margin-right: 0; }
    .promo input[type='text'].own-margin {
      margin-bottom: 25px; }
  .promo button {
    width: 100%;
    order: 3; }
  .promo__errors {
    order: 2; }
    .promo__errors .validation-result {
      margin-bottom: 10px; }
  .promo__discount {
    order: 2; } }

@media (min-width: 1440px) {
  .promo input[type='text'] {
    width: 55%;
    margin-right: 20px; }
  .promo button {
    padding-left: 16px;
    padding-right: 16px;
    width: 39%;
    order: 2; }
  .promo__errors {
    order: 3; }
  .promo__discount {
    order: 3; } }
