.constructor__count-hint {
  position: absolute;
  font-family: TT Norms;
  font-size: 15px;
  line-height: 18px;
  color: #808080;
  bottom: -33px;
  white-space: nowrap;
  font-weight: 400; }
  .constructor__count-hint.right-0 {
    right: 0; }

.constructor__count-hint-mobile {
  right: 2%;
  bottom: unset; }

@media (min-width: 440px) {
  .constructor__count-hint-mobile {
    left: 312px; } }

@media (min-width: 550px) {
  .constructor__count-hint-mobile {
    left: 412px; } }

@media screen and (min-width: 769px) {
  .constructor__count-hint-mobile {
    display: none; } }

@media screen and (min-width: 992px) {
  .constructor__count-hint.right-0 {
    right: unset; } }
