.slider-item {
  width: 243px;
  max-width: 530px;
  font-family: TT Norms;
  margin-bottom: 20px;
  border-radius: 8px;
  background: #FFEDF3;
  padding: 15px;
  box-sizing: border-box;
  margin-left: 20px; }
  .slider-item__text {
    margin: 0;
    color: #808080;
    font-size: 16px;
    line-height: 22px; }
    .slider-item__text_red {
      color: #f8819c;
      font-weight: bold; }

@media (min-width: 576px) {
  .slider-item:first-child {
    margin-left: 0; } }

@media (min-width: 768px) {
  .slider-item {
    width: 48%;
    box-sizing: border-box;
    padding: 25px 20px; }
    .slider-item:nth-child(even) {
      margin-left: 20px; }
    .slider-item:nth-child(odd) {
      margin-left: 0; }
    .slider-item__text {
      font-size: 15px;
      line-height: 18px; } }

@media (min-width: 1200px) {
  .slider-item {
    padding: 35px 30px; }
    .slider-item:nth-child(even), .slider-item:nth-child(odd) {
      margin-left: 20px; }
    .slider-item:first-child {
      margin-left: 0; } }
