.payment .payment__block.payment__block--half {
  padding: 10px 15px; }

.payment__block-input {
  background: #FFFFFF;
  border: 1px solid #E6E6E6;
  font-family: TT Norms;
  font-size: 15px;
  line-height: 18px;
  color: #3D3B3B;
  font-weight: 500;
  text-align: center;
  width: 60px;
  height: 40px; }
  .payment__block-input::-webkit-outer-spin-button, .payment__block-input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0; }
  .payment__block-input[type=number] {
    -moz-appearance: textfield; }

.payment__block-person-count-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%; }
  .payment__block-person-count-container .payment__block-title {
    font-size: 15px;
    line-height: 18px;
    padding-right: 0; }

@media (min-width: 576px) {
  .payment .payment__block.payment__block--half {
    padding: 20px; }
  .payment__block-input {
    width: 80px; } }

@media (min-width: 769px) {
  .payment__block-person-count-container {
    display: block; }
    .payment__block-person-count-container .payment__block-title {
      font-size: 36px;
      line-height: 42px; } }

@media (min-width: 992px) {
  .payment .payment__block.payment__block--half {
    padding: 35px 40px; } }

@media (min-width: 1440px) {
  .payment__block-person-count-container .payment__block-title {
    font-size: 35px;
    padding-bottom: 17px; } }
